:root {
  --black: #000;
  --not-so-black: #121212;
  --white: #fff;
  --grey: #ababab;
  --yellow: #f8d34f;
  --pastel: #84a178;
  --form-border: #0a8a17;
  --form-bg: #f8d34f1a;
}

.menu-container {
  background-color: var(--pastel);
  position: fixed;
  inset: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-mob__header {
  display: flex;
}

.logo {
  position: absolute;
  top: 12px;
  left: 32px;
}

.menu-mob__close {
  cursor: pointer;
  background-color: var(--pastel);
  border: none;
  margin-left: auto;
  position: absolute;
  top: 16px;
  right: 20px;
}

.menu-mob__nav {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 110px;
}

.menu-mob__item:not(:last-child) {
  margin-bottom: 32px;
}

.menu-mob__link {
  color: var(--white);
  font-size: 32px;
  font-weight: 700;
}

.menu-mob__link:hover, .menu-mob__link:focus {
  color: var(--black);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.menu-mob__buy {
  text-align: center;
  background-color: var(--yellow);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin: 0 auto;
  padding: 12px 36px;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.menu-mob__buy:hover {
  background-color: var(--form-border);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.menu-mob__buy:focus {
  background-color: var(--form-border);
  color: var(--white);
  fill: var(--white);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.menu-mob__buy:disabled {
  background-color: var(--grey);
}

.adv-layout {
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-bottom: 28px;
  display: flex;
}

.adv-layout:last-child {
  margin-bottom: 0;
}

.icon-background {
  color: var(--white);
  background-color: var(--yellow);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 7px;
  display: flex;
}

.adv-first-paragraph {
  color: var(--white);
  letter-spacing: 1.44px;
  text-transform: uppercase;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.adv-sec-paragraph {
  color: var(--white);
  width: 230px;
  text-align: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 170%;
}

html, div, span, object, iframe, p, blockquote, pre, abbr, acronym, address, big, cite, code, del, dfn, em, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, i, center, dl, dt, dd, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

ul, ol, menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  box-sizing: border-box;
  overflow: auto;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: #121212;
  background-color: var(--pastel);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  text-decoration: none;
}

